.searchbar{
    display: flex;
    margin-top: 50px;
    padding: 30px;
    background-color: grey;
    border-radius: 10px;
    height: auto;
    width: 50%;
}

.searchbar_button{
    width: 25%;
    height: auto;
    padding: 10px;
    border-radius: 10px;
    border: none;
    background-color: white;
    cursor: pointer;

}

.searchbar_input{
width: 65%;
height: auto;
padding: 10px;
background-color: white;
border-radius: 10px;
border: none;
}